.error {
  background-color: var(--background-color);
  color: var(--text-color);
  &__container {
    padding-top: 177px;
    text-align: center;
  }
  &__title {
    margin-bottom: 62px;
    font-family: 'DelaGothicOne', sans-serif;
    font-weight: 400;
    font-size: 100px;
    line-height: 140.8%;
  }
  &__text {
    margin-bottom: 62px;
    font-weight: 400;
    font-size: 35px;
    line-height: 42px;
  }
  &__image {
    margin-bottom: 62px;
  }
}

@media (max-width: (800px)) {
  .error {
    &__title {
      font-size: 70px;
    }
  }
}

@media (max-width: (650px)) {
  .error {
    &__title {
      font-size: 60px;
    }
    &__text {
      font-size: 30px;
    }
  }
}

@media (max-width: (550px)) {
  .error {
    &__title {
      font-size: 40px;
    }
    &__text {
      font-size: 20px;
    }
  }
}
