.nav {
  padding-right: 20px;
  &__list {
    @include flex-all-sb;
  }
  &__item {
    @include mr(10vw);
    list-style-type: none;
  }
  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--text-color);
  }
  .active {
    color: var(--active-btn);
  }
}

@media (max-width: (1300px)) {
  .nav {
    &__link {
      font-size: 18px;
    }
  }
}

@media (max-width: (1024px)) {
  .nav {
    &__item {
      @include mr(8vw);
    }
  }
}

@media (max-width: (840px)) {
  .nav {
    position: fixed;
    right: 45px;
    top: 90px;
    z-index: 500;
    width: 180px;
    height: 225px;
    background-color: var(--background-color);
    border: 2px solid var(--element-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    &__list {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      width: 100%;
    }
    &__item {
      margin-bottom: 15px;
    }
    &__link {
      font-size: 16px;
    }
  }
  .menu--active {
    opacity: 1;
    visibility: visible;
  }
}
