.book-about {
  padding-top: 240px;
  background-color: var(--background-color);
  &__image {
    display: flex;
    justify-content: space-between;
  }
  &__info{
    @include flex-all-sb;
    padding: 50px 28px;
    .btn__link {
      font-weight: 600;
      font-size: 18px;
      line-height: 24px;
      color: var(--text-color);
    }
  }
  &__text {
    display: inline-block;
    max-width: 415px;
    font-weight: 400;
    font-size: 22px;
    line-height: 27px;
    color: var(--text-color);
  }
}


@media (max-width: (890px)) {
  .book-about {
    &__text {
      max-width: 350px;
      font-size: 18px;
    }
  }
}

@media (max-width: (830px)) {
  .book-about {
    &__text {
      max-width: 300px;
      font-size: 16px;
    }
  }
}

@media (max-width: (750px)) {
  .book-about {
    padding: 0 50px;
    &__image {
      flex-direction: column-reverse;
      .image {
        margin-top: 50px;
      }
    }
    &__info {
      flex-direction: column;
      align-items: flex-start;
    }
    &__text {
      margin-bottom: 30px;
    }
  }
}

@media (max-width: (550px)) {
  .book-about {
    &__info {
      .btn {
        min-width: 250px;
        padding: 25px 25px;
      }
    }
  }
}
