.hero {
  background-color: var(--background-color);
  &__container {
    @include flex
  }
  &__content {
    max-width: 430px;
    margin-top: 240px;
  }
  .image {
    width: 100%;
    height: 100%;
  }
  &__title {
    font-family: 'DelaGothicOne', sans-serif;
    font-weight: 400;
    font-size: 55px;
    line-height: 140.8%;
    color: var(--text-color);
  }
  &__text {
    display: block;
    padding-bottom: 50px;
    max-width: 66.6%;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--text-color);
  }
}

@media (max-width: (1300px)) {
  .hero {
    &__content {
      margin-top: 140px;
    }
  }
}

@media (max-width: (1100px)) {
  .hero {
    &__title {
      font-size: 40px;
      line-height: 120%;
    }
  }
}

@media (max-width: (1000px)) {
  .hero {
    &__title {
      font-size: 30px;
      line-height: 120%;
    }
    &__text {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

@media (max-width: (840px)) {
  .hero {
    &__container {
      justify-content: center;
      align-items: center;
      flex-wrap: wrap-reverse;
    }
    &__image {
      margin-top: -70px;
    }
    &__content {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      max-width: 700px;
      margin-top: 170px;
      z-index: 1;
    }
    &__title {
      max-width: 290px;
      max-height: 210px;
      margin: 0;
    }
    &__text {
      max-width: 200px;
      max-height: 92px;
      font-size: 14px;
    }
  }
}

@media (max-width: (500px)) {
  .hero {
    &__container {
      justify-content: center;
      align-items: center;
      flex-wrap: wrap-reverse;
    }
    &__content {
      display: block;
    }
    &__title {
      max-width: 290px;
      max-height: 210px;
      margin-bottom: 20px;
    }
    &__text {
      padding-left: 150px;
      max-width: 350px;
      max-height: 92px;
      font-size: 14px;
    }
  }
}
