.hero-zones {
  background-color: var(--background-color);
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-left: 20px;
  }
  &__content {
    max-width: 714px;
    padding-top: 200px;
  }
  &__title {
    margin-bottom: 90px;
    font-weight: 400;
    font-size: 40px;
    line-height: 57px;
    color: var(--text-color);
  }
  &__text {
    display: block;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
  }
}

@media (max-width: (1700px)) {
  .hero-zones {
    &__content {
      max-width: 550px;
      padding-right: 50px;
    }
    &__text {
      font-size: 22px;
    }
  }
}

@media (max-width: (1120px)) {
  .hero-zones {
    &__content {
      max-width: 450px;
    }
    &__title {
      margin-bottom: 70px;
      font-size: 37px;
    }
    &__text {
      font-size: 20px;
    }
  }
}

@media (max-width: (1000px)) {
  .hero-zones {
    &__content {
      max-width: 400px;
    }
    &__title {
      margin-bottom: 50px;
      font-size: 34px;
    }
    &__text {
      font-size: 18px;
    }
  }
}

@media (max-width: (890px)) {
  .hero-zones {
    &__container {
      flex-direction: column-reverse;
    }
    &__image {
      margin-top: 150px;
    }
    &__content {
      max-width: 714px;
      padding-top: 50px;
    }
  }
}

@media (max-width: (830px)) {
  .hero-zones {
    &__title {
      margin-bottom: 30px;
      font-size: 38px;
    }
    &__text {
      font-size: 14px;
    }
  }
}
