.zones {
  position: relative;
  background-color: var(--background-color);
  &__content {
    margin: 0 220px;
    padding-bottom: 80px;
  }
  &__heading {
    padding: 250px 0 80px 0;
    text-align: center;
    font-family: 'DelaGothicOne';
    font-weight: 400;
    font-size: 47px;
    line-height: 140.8%;
    color: var(--text-color);
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    margin: 70px 0;
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    color: var(--text-color);
  }
  &__btn--prev {
    position: absolute;
    left: 70px;
    bottom: 40%;
    width: 50px;
    height: 50px;
    border: 2px solid var(--element-color);
    border-width: 2px 0 0 2px;
    transform: rotate(-45deg);
  }
  &__btn--next {
    position: absolute;
    right: 70px;
    bottom: 40%;
    width: 50px;
    height: 50px;
    border: 2px solid var(--element-color);
    border-width: 0 2px 2px 0;
    transform: rotate(-45deg);
  }
}


@media (max-width: (1120px)) {
  .zones {
    &__heading {
      font-size: 37px;
    }
    &__title {
      font-size: 24px;
    }
  }
}

@media (max-width: (980px)) {
  .zones {
    &__content {
      margin: 0 150px;
    }
    &__title {
      font-size: 20px;
    }
    &__btn--prev {
      left: 50px;
      width: 40px;
      height: 40px;
    }
    &__btn--next {
      right: 50px;
      width: 40px;
      height: 40px;
    }
  }
}

@media (max-width: (890px)) {
  .zones {
    &__heading {
      font-size: 30px;
    }
  }
}

@media (max-width: (850px)) {
  .zones {
    &__content {
      margin: 0 100px;
    }
    &__btn--prev {
      left: 30px;
      width: 30px;
      height: 30px;
    }
    &__btn--next {
      right: 30px;
      width: 30px;
      height: 30px;
    }
  }
}

@media (max-width: (768px)) {
  .zones {
    &__heading {
      padding: 100px 0 80px 0;
    }
  }
}

@media (max-width: (525px)) {
  .zones {
    &__heading {
      font-size: 20px;
    }
    &__content {
      margin: 0 50px;
    }
    &__title {
      font-size: 18px;
    }
    &__btn--prev {
      left: 20px;
      width: 20px;
      height: 20px;
    }
    &__btn--next {
      right: 20px;
      width: 20px;
      height: 20px;
    }
  }
}
