.products {
  background-color: var(--background-color);
  &__container {
    max-width: 1480px;
    height: 100%;
    padding: 150px 40px 40px 0;
  }
  &__title {
    padding-bottom: 50px;
    font-family: 'DelaGothicOne';
    font-weight: 400;
    font-size: 40px;
    line-height: 140.8%;
    text-align: center;
    color: var(--text-color);
  }
}
.grid-container {
  width: 100%;
}
.product-grid {
  display: grid;
  grid-gap: 50px;
  grid-template-columns: repeat(3, 1fr);
  &__item {
    list-style-type: none;
  }
  .product {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__title {
      padding: 30px 0 15px 0;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: var(--text-color);
    }
    &__price {
      padding: 15px 0 30px 0;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: var(--text-color);
    }
  }
}

@media (max-width: (1400px)) {
  .products {
    .product-grid {
      grid-gap: 20px;
    }
    .product {
      &__title {
        font-size: 18px;
      }
      &__price {
        font-size: 18px;
      }
    }
    .btn {
      min-width: 238px;
      padding: 25px 35px;
    }
  }
}


@media (max-width: (1200px)) {
  .products {
    &__title {
      font-size: 30px;
    }
  }
  .product-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: (840px)) {
  .products {
    .product {
      &__title {
        font-size: 16px;
      }
      &__price {
        font-size: 16px;
      }
    }
  }
}

@media (max-width: (690px)) {
  .products {
    .product-grid {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}
