.contacts {
  background-color: var(--background-color);
  &__container {
    @include flex-all-sb;
    padding: 150px 20px 40px 20px;
  }
  &__content {
    color: var(--text-color);
  }
  &__title {
    font-weight: 400;
    font-size: 47px;
    line-height: 57px;
  }
  &__subtitle {
    font-weight: 600;
    font-size: 30px;
    line-height: 36px;
  }
  &__text {
    min-width: 350px;
    font-weight: 400;
    font-size: 20px;
    line-height: 36px;
  }
  &__image {
    margin-top: 100px;
  }
}

@media (max-width: (760px)) {
  .contacts {
    &__container {
      flex-direction: column;
    }
  }
}
