.burger {
  display: none;
}

@media (max-width: (840px)) {
  .burger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 23px;
    height: 23px;
    background-color: var(--background-color);
    border-radius: 0;
    border-color: var(--background-color);
    margin-right: 30px;

    &__line {
      width: 23px;
      height: 3px;
      background-color: var(--element-color);
    }
  }
}

