.events-about {
  position: relative;
  background-color: var(--background-color);
  &__content {
    margin: 0 220px;
    padding: 150px 0 80px 0;
  }
  &__item {
    display: flex;
    justify-content: space-around;
    .image {
      padding-left: 2px;
    }
  }
  &__post {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title {
    margin: 20px 0 100px 0;
    font-weight: 400;
    font-size: 47px;
    line-height: 57px;
    color: var(--text-color);
  }
  &__text {
    margin-bottom: 120px;
    max-width: 530px;
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    color: var(--text-color);
  }
  &__btn--prev {
    position: absolute;
    left: 70px;
    bottom: 50%;
    width: 50px;
    height: 50px;
    border: 2px solid var(--element-color);
    border-width: 2px 0 0 2px;
    transform: rotate(-45deg);
  }
  &__btn--next {
    position: absolute;
    right: 70px;
    bottom: 50%;
    width: 50px;
    height: 50px;
    border: 2px solid var(--element-color);
    border-width: 0 2px 2px 0;
    transform: rotate(-45deg);
  }
}


@media (max-width: (1400px)) {
  .events-about {
    &__item {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}


@media (max-width: (1120px)) {
  .events-about {
    &__post {
      .btn {
        min-width: 238px;
        padding: 25px 35px;
      }
    }
    &__title {
      font-size: 37px;
    }
    &__text {
      max-width: 360px;
      font-size: 20px;
    }
  }
}

@media (max-width: (900px)) {
  .events-about {
    &__content {
      margin: 0 180px;
    }
    &__title {
      margin: 20px 0 50px 0;
    }
    &__text {
      margin-bottom: 60px;
    }
  }
}

@media (max-width: (840px)) {
  .events-about {
    &__content {
      margin: 0 150px;
    }
    &__text {
      max-width: 340px;
      font-size: 18px;
    }
    &__btn--prev {
      left: 50px;
      width: 30px;
      height: 30px;
    }
    &__btn--next {
      right: 50px;
      width: 30px;
      height: 30px;
    }
  }
}

@media (max-width: (650px)) {
  .events-about {
    &__content {
      margin: 0 100px;
    }
    &__btn--prev {
      left: 30px;
    }
    &__btn--next {
      right: 30px;
    }
  }
}

@media (max-width: (550px)) {
  .events-about {
    &__content {
      margin: 0 50px;
    }
    &__text {
      font-size: 16px;
    }
    &__btn--prev {
      left: 20px;
    }
    &__btn--next {
      right: 20px;
    }
  }
}

