.basket {
  background-color: var(--background-color);
  color: var(--text-color);
  &__container {
    display: grid;
    grid-template-columns: 60% auto;
    padding: 150px 20px 40px 20px;
  }
  &__products {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 20px;
  }
  .product {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(4, 1fr);
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    &__number {
      input {
        width: 40px;
        padding-left: 5px;
        font-family: var(--font-family);
        background-color: var(--background-color);
        border: 1px solid var(--element-color);
        border-radius: 5px;
        color: var(--text-color);
      }
    }
  }
  &__total {
    display: flex;
    justify-content: flex-end;
    padding-right: 15%;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    span{
      margin-right: 10px;
    }
  }
  &__ordering {
    padding-left: 100px;
    h2 {
      margin-bottom: 40px;
    }
    input {
      font-family: var(--font-family);
      font-weight: 400;
      color: var(--text-color);
      font-size: 20px;
      line-height: 24px;
      background-color: var(--background-color);
      border: 1px solid var(--element-color);
      padding-left: 10px;
    }
    .field__big {
      display: block;
      width: 478px;
      height: 68px;
      margin-bottom: 50px;
    }
    .field__small {
      width: 225px;
      height: 68px;
      margin: 0 25px 50px 0;
    }
  }
}


@media (max-width: (1545px)) {
  .basket {
    &__ordering {
      padding-left: 20px;
    }
  }
}

@media (max-width: (1435px)) {
  .basket {
    &__container {
      grid-template-columns: 50% auto;
    }
  }
}

@media (max-width: (1090px)) {
  .basket {
    &__container {
      grid-template-columns: auto;
    }
    &__ordering {
      padding-top: 70px;
    }
    input {
      font-size: 18px;
    }
  }
}

@media (max-width: (570px)) {
  .basket {
    .product {
      font-size: 16px;
      .image {
        padding-right: 10px;
      }
    }
    &__ordering {
      .field__big {
        width: 326px;
      }
      .field__small {
        width: 148px;
      }
    }
    input {
      font-size: 16px;
    }
  }
}
