.hero-about {
  background-color: var(--background-color);
  &__container {
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
  }
  &__image {
    padding-top: 140px;
  }
  &__content {
    max-width: 767px;
    padding-top: 200px;
    .btn__link {
      font-weight: 600;
      font-size: 18px;
      line-height: 24px;
      color: var(--text-color);
    }
  }
  &__title {
    margin-bottom: 90px;
    font-weight: 400;
    font-size: 40px;
    line-height: 57px;
    color: var(--text-color);
  }
  &__text {
    display: block;
    margin-bottom: 70px;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
  }
}


@media (max-width: (1700px)) {
  .hero-about {
    &__content {
      max-width: 567px;
    }
  }
}

@media (max-width: (1120px)) {
  .hero-about {
    &__content {
      max-width: 467px;
    }
    &__title {
      margin-bottom: 70px;
      font-size: 37px;
    }
    &__text {
      font-size: 20px;
    }
  }
}

@media (max-width: (1000px)) {
  .hero-about {
    &__content {
      max-width: 350px;
    }
    &__title {
      margin-bottom: 50px;
      font-size: 34px;
    }
    &__text {
      font-size: 18px;
    }
  }
}

@media (max-width: (890px)) {
  .hero-about {
    &__content {
      max-width: 310px;
    }
    &__title {
      font-size: 30px;
    }
    &__text {
      margin-bottom: 50px;
      font-size: 16px;
    }
  }
}

@media (max-width: (830px)) {
  .hero-about {
    &__content {
      max-width: 270px;
      .btn {
        min-width: 258px;
        padding: 25px 45px;
      }
    }
    &__title {
      margin-bottom: 30px;
      font-size: 28px;
    }
    &__text {
      margin-bottom: 30px;
      font-size: 14px;
    }
  }
}

@media (max-width: (750px)) {
  .hero-about {
    padding: 0 50px;
    &__content {
      padding-top: 50px;
    }
    &__container {
      flex-direction: column-reverse;
      align-items: flex-start;
    }
  }
}
