:root {
  // base
  --font-family: "Inter", sans-serif;
  --content-width: 1780px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --background-color: #000000;
  --text-color: #ffffff;
  --element-color: #ffffff;
  --active-btn: #800000;
}
