.btn {
  min-width: 298px;
  padding: 25px 65px;
  border: 1px solid var(--element-color);
  background-color: var(--background-color);
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: var(--text-color);
  cursor: pointer;
  &:hover {
    background-color: var(--active-btn);
  }
}
